@import url('https://fonts.googleapis.com/css?family=Abel|Montserrat');
html,body {

    height: 100%;
    width: 100%;
    margin : 0px;
    padding : 0px;
}
#chat-widget-root{
  position:relative;
  height: 300px;
  width: 100%;
  padding-top: 0;


  .enter-chat{
    position:relative;
    height: 100%;
    width: 100%;
      background: #fff;
      display: flex;
      align-items: center!important;
      justify-content: center!important;
      form{
          flex: 0 0 33.333333%;
          max-width: 33.333333%;
          .input-group{
              position: relative;
              display: -ms-flexbox;
              display: flex;
              -ms-flex-wrap: wrap;
              flex-wrap: wrap;
              -ms-flex-align: stretch;
              align-items: stretch;
              width: 100%;
              > .form-control{
                  position: relative;
                  -ms-flex: 1 1 auto;
                  flex: 1 1 auto;
                  width: 1%;
                  margin-bottom: 0;
                  display: block;
                  height: calc(1.5em + .75rem + 2px);
                  padding: .375rem .75rem;
                  font-size: 1rem;
                  font-weight: 400;
                  line-height: 1.5;
                  color: #495057;
                  background-color: #fff;
                  background-clip: padding-box;
                  border: 1px solid #ced4da;
                  border-radius: .25rem;
                  transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
              }
              .input-group-append {
                  margin-left: -1px;
                  display: flex;
                  .btn{
                    border-top-left-radius: 0;
                    border-bottom-left-radius: 0;
                      outline: none;
                      position: relative;
                      z-index: 2;
                      color: #6c757d;
                      border-color: #6c757d;
                      display: inline-block;
                      font-weight: 400;
                      text-align: center;
                      vertical-align: middle;
                      -webkit-user-select: none;
                      -moz-user-select: none;
                      -ms-user-select: none;
                      user-select: none;
                      background-color: transparent;
                      padding: .375rem .75rem;
                      font-size: 1rem;
                      line-height: 1.5;
                      border-radius: .25rem;
                      transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
                  }
              }
          }
      }
  }
.chat-widget-navbar{
  text-align: center;
    background: #202323;
    height: 46px;
    p{
        line-height: 46px;
        font-size: 18px;
      color: #fff;
      margin: 0 auto;
    }
}
  .chat-widget{
    display: block;
    position:relative;
    height: 100%;
    width: 100%;
      max-width: 800px;
      margin: 0px auto;
      border-radius: 12px;
      overflow: hidden;
      background: #fff;


    &-messages{
      background: #fff;
      overflow:auto;
      border-radius: 12px 12px 0 0;
      padding: 0;
      -webkit-flex-direction: row;
      flex-direction: row;
      width: 100%;

      scrollbar-face-color: #559FEA;
      scrollbar-highlight-color: #559FEA;
      scrollbar-shadow-color: #559FEA;
      scrollbar-3dlight-color: #559FEA;
      scrollbar-arrow-color: #559FEA;
      scrollbar-track-color: #559FEA;
      scrollbar-darkshadow-color: #559FEA;
        ::-webkit-scrollbar {
            width:5px;
            height: 5px;
        }

        ::-webkit-scrollbar-button:start:decrement,
        ::-webkit-scrollbar-button:end:increment  {
            height: 5px;
            width: 5px;
            display:block;
            background-color: #AAA;
        }

      .loadMessages{
            display: flex;
            flex-direction: column-reverse;
      }

      .message{
        padding: 10px;
        display: block;
        min-height: 50px;
        height : auto !important;
        &:after {
            clear: both;
        }
        &.right{
            .avatar{
                float: right;
            }
            .data{
                text-align: right;
            }
        }
        .avatar{
            width: 40px;
            height: 40px;
            line-height: 40px;
            border-radius: 50%;
            text-align: center;
            margin: 0 10px 0 0;
            display: inline-block;
            vertical-align: top;
            overflow: hidden;

          img{
            max-width: 100%;
            max-height: 100%;
          }
        }
          .head-message {
              position: relative;
              .username {
                  font-weight: 800;
                  margin: 5px 0;
                  padding-right: 80px;
              }
              .time-label{
                  position: absolute;
                  right: 0;
                  top: 0;
                  color:#727272;
                  font-size: 12px;
              }
          }
        .data{
          display: inline-block;
            width: calc(100% - 60px);
            vertical-align: top;
            .text{
                word-break: break-word;
            }
        }

      }

      .no-message{
        margin: 10px;
      }
    }
    &-wrapper-message{
        background:#202323;
        &:after{
         display: block;
         clear: both;
         content: '';
        }
          &:before{
              content: "";
              position: absolute;
              bottom: 50px;
              height: 20px;
              width: calc(100% - 75px);
              right: 20px;
              background: linear-gradient(rgba(255, 255, 255, 0.2), white);
              z-index: 10;
              margin: 0 auto;
          }
      }
    &-chatbox{
      position:absolute;
      bottom: 10px;
      padding: 0;
        width: 100%;
    }
    &-message-container{
          width: 100%;
          display: flex;
          .form-col{
              position: relative;
              width: 100%;
              height: 40px;
              margin: 0 10px;
              input, div {
                  width: 100%;
                  height: 100%;
                  outline: none;
              }
              input{
                  padding: 0 40px 0 12px;
                  border-radius:8px ;
                  background: #f2f1f1;
                  box-shadow: none;
                  border: none;
                  box-sizing: border-box;
              }
              button{
                  position: absolute;
                  right: 0;
                  top: 0;
                  background: none;
                  outline: none;
                  border: none;
                  box-shadow: none;
                  height: 100%;
                  width: 40px;
              }
          }
      }
    .emoji-mart {
          position: absolute;
          bottom: 46px;
          right: 0;
          .emoji-mart-bar:last-child {
              display: none;
          }
      }

    .inputContainer{
          width: 40px;
          position: relative;
          input{
              position: absolute;
              top: 0;
              left: 0;
              width: 100%;
              height: 100%;
              opacity: 0;
              padding: 14px 0;
              cursor: pointer;
          }
          label{
              display: block;
              padding: 10px 5px;
              background: #fff;
              color: #fff;
              transition: all .4s;
              cursor: pointer;
              margin: 0;
              width: 100%;
              height: 100%;
              text-align: center;
          }
      }
  }

}

.messages::-webkit-scrollbar-track
{
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    background-color: transparent;


}

.messages::-webkit-scrollbar
{
    width: 4px;
    background-color: transparent;
}

.messages::-webkit-scrollbar-thumb
{
    background-color: #202323;
    z-index: 100;
}
